
export default {
	methods: {
		hasLogin() {
			const userInfo = uni.getStorageSync('userInfo')
			if (userInfo) {
				uni.switchTab({
					url: 'pages/workbench/workbench'
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请先登录'
				})
				uni.reLaunch({
					url: 'pages/login/login'
				})
				uni.hideHomeButton()
			}
		}
	},
	onLaunch: function() {
		console.log('App Launch')
	},
	onShow: function() {
		this.hasLogin()
	},
	onHide: function() {
		console.log('App Hide')
	}
}
